import React, { useEffect } from "react";
import posed from "react-pose";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

const Container = posed.div({
  enter: {
    staggerChildren: 50
  }
});

export default function Portfolios({ data }) {
  useEffect(() => {
    document.title = `rfun - ${data.lang.menus[3]}`
  })
  const Portfolio = data.portfolios.map((item, i) => (
    <div className="col-md-4 col-sm-6" key={i}>
      <div className={"service-block id" + i}>
        <div className="service-info">
          <div className="service-image">
            <a href={item.linkDemo} title={item.linkDemo === '#' ? "Demo isn't available" : "Click for demo !"}>
              <span
                className={"fa " + item.faIcon}
              ></span>
            </a>
          </div>
        </div>
        <h5 dangerouslySetInnerHTML={{ __html: item.title }}></h5>
        <p className="text-primary">{item.tech}</p>
      </div>
    </div>
  ));
  return (
    <Container>
      <div id="services">
        <El>
          <div className="header">
            <h1>{data.lang.menus[3]}</h1>
          </div>
        </El>
        <El>
          <div className="content">
            <div className="row justify-content-center">
              {Portfolio}
            </div>
          </div>
        </El>
      </div>
    </Container>
  );
}
