import React, { useEffect, useState } from "react";
import posed from "react-pose";
import Typed from "react-typed";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

export default function Home({ data }) {

  const [linkWa, setLinkWa] = useState("https://web.whatsapp.com/send?phone=6281907456710&text=Hii%20Arfan,%20")
  const pp = require("../assets/images/arfan.webp")

  useEffect(() => {
    (navigator.userAgent.match(/Android/i) ||
      navigator.userAgent.match(/webOS/i) ||
      navigator.userAgent.match(/iPhone/i) ||
      navigator.userAgent.match(/iPad/i) ||
      navigator.userAgent.match(/iPod/i) ||
      navigator.userAgent.match(/BlackBerry/i) ||
      navigator.userAgent.match(/Windows Phone/i))
      && setLinkWa("https://api.whatsapp.com/send?phone=6281907456710&text=Hii%20Arfan,%20")
  }, [linkWa])

  const { lang, links, name } = data
  return (
    <El>
      <div id="home">
        <div className="header">
          <div className="row">
            <div id="photo" className="col-md-6">
              <img
                src={pp}
                style={{ width: 270, height: 270 }}
                alt="Profile"
              />
            </div>
            <div id="id1" className="col-md-6">
              <h1 id="name">{name.first}{name.middle ? ' ' + name.middle : ''}{name.last ? ' ' + name.last : ''}</h1>
              <Typed
                className="prof"
                strings={lang.profession || ["Profession Not defined"]}
                typeSpeed={50}
                backSpeed={30}
                backDelay={10000}
                startDelay={1000}
                cursorChar={"_"}
                loop
              />
              <div id="links">
                <a href={links.github}>
                  <span className="fa fa-github" />
                </a>
                <a href={links.facebook}>
                  <span className="fa fa-facebook-square" />
                </a>
                <a href={links.wordpress}>
                  <span className="fa fa-wordpress" />
                </a>
                <a href={links.linkedin}>
                  <span className="fa fa-linkedin-square" />
                </a>
                <a href={linkWa}>
                  <span className="fa fa-whatsapp" />
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="content">
          <div className="row justify-content-center">
            <div className="col-sm-12 col-md-8 sayHi">
              {lang.about}
              <a className="text-success" href={linkWa}>
                {" " + lang.sayHi}
              </a>
            </div>
          </div>
        </div>
      </div>
    </El>
  );
}
